
"use client"

import * as React from 'react';
import Link from 'next/link';
import AppLayout from '@/components/app-layout';
import { Button } from '@/components/ui/button';
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from "@/components/ui/alert-dialog"
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { DropdownMenu, DropdownMenuContent, DropdownMenuItem, DropdownMenuTrigger } from '@/components/ui/dropdown-menu';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { Badge } from '@/components/ui/badge';
import { MoreHorizontal, PlusCircle, Loader2 } from 'lucide-react';
import type { Question, Subject } from '@/types';
import { Input } from '@/components/ui/input';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { collection, query, where, getDocs, deleteDoc, doc, onSnapshot } from "firebase/firestore";
import { db, auth } from "@/lib/firebase";
import { useAuthState } from 'react-firebase-hooks/auth';
import { useToast } from '@/hooks/use-toast';

export default function QuestionsPage() {
    const [user] = useAuthState(auth);
    const { toast } = useToast();
    const [questions, setQuestions] = React.useState<Question[]>([]);
    const [subjects, setSubjects] = React.useState<Subject[]>([]);
    const [loading, setLoading] = React.useState(true);
    const [subjectFilter, setSubjectFilter] = React.useState<string>("all");
    const [difficultyFilter, setDifficultyFilter] = React.useState<string>("all");
    const [searchTerm, setSearchTerm] = React.useState<string>("");

    React.useEffect(() => {
        if (!user) {
            setLoading(false);
            return;
        }

        const questionsQuery = query(collection(db, 'users', user.uid, 'questions'));
        const unsubscribeQuestions = onSnapshot(questionsQuery, (querySnapshot) => {
            const questionsData = querySnapshot.docs.map(doc => {
                const data = doc.data();
                return {
                    id: doc.id,
                    ...data,
                    createdAt: (data.createdAt as any)?.toDate() ?? new Date()
                } as Question;
            });
            setQuestions(questionsData);
            setLoading(false);
        }, (error) => {
            console.error("Error fetching questions:", error);
            toast({ title: "Error", description: "Could not fetch questions.", variant: "destructive" });
            setLoading(false);
        });

        const subjectsQuery = query(collection(db, 'users', user.uid, 'subjects'));
        const unsubscribeSubjects = onSnapshot(subjectsQuery, (querySnapshot) => {
            const subjectsData = querySnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() } as Subject));
            setSubjects(subjectsData);
        }, (error) => {
            console.error("Error fetching subjects:", error);
            toast({ title: "Error", description: "Could not fetch subjects.", variant: "destructive" });
        });
        
        return () => {
            unsubscribeQuestions();
            unsubscribeSubjects();
        };

    }, [user, toast]);

    const deleteQuestion = async (questionId: string) => {
        if (!user) return;
        try {
            await deleteDoc(doc(db, "users", user.uid, "questions", questionId));
            toast({ title: "Success", description: "Question deleted successfully." });
        } catch (error) {
            console.error("Error deleting question:", error);
            toast({ title: "Error", description: "Could not delete question.", variant: "destructive" });
        }
    }

    const filteredQuestions = React.useMemo(() => {
        return questions
            .filter(q => subjectFilter === 'all' || q.subject === subjectFilter)
            .filter(q => difficultyFilter === 'all' || q.difficulty === difficultyFilter)
            .filter(q => q.question.toLowerCase().includes(searchTerm.toLowerCase()));
    }, [questions, subjectFilter, difficultyFilter, searchTerm]);

    return (
        <AppLayout>
            <div className="space-y-6">
                <div className="flex items-center justify-between">
                    <div>
                        <h1 className="text-3xl font-bold font-headline">My Questions</h1>
                        <p className="text-muted-foreground">Manage your personal question bank.</p>
                    </div>
                    <Button asChild>
                        <Link href="/questions/new">
                            <PlusCircle className="mr-2 h-4 w-4" />
                            Add New Question
                        </Link>
                    </Button>
                </div>
                
                <Card>
                    <CardHeader>
                        <CardTitle>All Questions</CardTitle>
                        <CardDescription>Browse, filter, and manage your saved questions.</CardDescription>
                         <div className="mt-4 flex flex-col md:flex-row gap-4">
                            <Input 
                                placeholder="Search questions..." 
                                className="max-w-sm"
                                value={searchTerm}
                                onChange={(e) => setSearchTerm(e.target.value)}
                            />
                            <Select value={subjectFilter} onValueChange={setSubjectFilter}>
                                <SelectTrigger className="w-full md:w-[180px]">
                                    <SelectValue placeholder="Filter by subject" />
                                </SelectTrigger>
                                <SelectContent>
                                    <SelectItem value="all">All Subjects</SelectItem>
                                    {subjects.map(s => <SelectItem key={s.id} value={s.name}>{s.name}</SelectItem>)}
                                </SelectContent>
                            </Select>
                            <Select value={difficultyFilter} onValueChange={setDifficultyFilter}>
                                <SelectTrigger className="w-full md:w-[180px]">
                                    <SelectValue placeholder="Filter by difficulty" />
                                </SelectTrigger>
                                <SelectContent>
                                    <SelectItem value="all">All Difficulties</SelectItem>
                                    <SelectItem value="easy">Easy</SelectItem>
                                    <SelectItem value="medium">Medium</SelectItem>
                                    <SelectItem value="hard">Hard</SelectItem>
                                </SelectContent>
                            </Select>
                        </div>
                    </CardHeader>
                    <CardContent>
                        <div className="border rounded-md">
                            <Table>
                                <TableHeader>
                                    <TableRow>
                                        <TableHead>Question</TableHead>
                                        <TableHead>Subject</TableHead>
                                        <TableHead>Difficulty</TableHead>
                                        <TableHead>Language</TableHead>
                                        <TableHead><span className="sr-only">Actions</span></TableHead>
                                    </TableRow>
                                </TableHeader>
                                <TableBody>
                                    {loading ? (
                                        <TableRow>
                                            <TableCell colSpan={5} className="text-center h-24">
                                                <Loader2 className="mx-auto h-8 w-8 animate-spin text-primary" />
                                            </TableCell>
                                        </TableRow>
                                    ) : filteredQuestions.length > 0 ? (
                                        filteredQuestions.map((q) => (
                                            <TableRow key={q.id}>
                                                <TableCell className="font-medium max-w-md truncate">{q.question}</TableCell>
                                                <TableCell>{q.subject}</TableCell>
                                                <TableCell><Badge variant="outline" className="capitalize">{q.difficulty}</Badge></TableCell>
                                                <TableCell><Badge variant="secondary">{(q.language || 'en').toUpperCase()}</Badge></TableCell>
                                                <TableCell>
                                                    <AlertDialog>
                                                        <DropdownMenu>
                                                            <DropdownMenuTrigger asChild>
                                                                <Button aria-haspopup="true" size="icon" variant="ghost">
                                                                    <MoreHorizontal className="h-4 w-4" />
                                                                    <span className="sr-only">Toggle menu</span>
                                                                </Button>
                                                            </DropdownMenuTrigger>
                                                            <DropdownMenuContent align="end">
                                                                <DropdownMenuItem asChild><Link href={`/questions/${q.id}/edit`}>Edit</Link></DropdownMenuItem>
                                                                <AlertDialogTrigger asChild>
                                                                    <DropdownMenuItem className="text-destructive focus:text-destructive focus:bg-destructive/10">Delete</DropdownMenuItem>
                                                                </AlertDialogTrigger>
                                                            </DropdownMenuContent>
                                                        </DropdownMenu>
                                                        <AlertDialogContent>
                                                            <AlertDialogHeader>
                                                            <AlertDialogTitle>Are you absolutely sure?</AlertDialogTitle>
                                                            <AlertDialogDescription>
                                                                This action cannot be undone. This will permanently delete your
                                                                question.
                                                            </AlertDialogDescription>
                                                            </AlertDialogHeader>
                                                            <AlertDialogFooter>
                                                            <AlertDialogCancel>Cancel</AlertDialogCancel>
                                                            <AlertDialogAction onClick={() => deleteQuestion(q.id)}>Delete</AlertDialogAction>
                                                            </AlertDialogFooter>
                                                        </AlertDialogContent>
                                                    </AlertDialog>
                                                </TableCell>
                                            </TableRow>
                                        ))
                                    ) : (
                                        <TableRow>
                                            <TableCell colSpan={5} className="text-center h-24">No questions found.</TableCell>
                                        </TableRow>
                                    )}
                                </TableBody>
                            </Table>
                        </div>
                    </CardContent>
                </Card>
            </div>
        </AppLayout>
    );
}
